<template>
    connecting to account...
</template>

<script>
    export default {
        name: 'connectView',
        components: {
        },
        created() {
            if (this.$route.query.token && this.$route.query.token != "" && this.$route.query.token != "undefined") {
                localStorage.token = this.$route.query.token;
                this.$router.push({path: '/dashboard'});
            }
            else {
                alert("No token received");
                this.$router.push({path: '/'});
            }
        }
    };
</script>
  
